@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.content{color:#000;text-align:center;margin-top:10%}.title{margin:10px;font-size:20px;color:#000}.row{margin:50px}.col{display:flex;justify-content:center;margin-bottom:50px}

.card{height:250px;width:175px;border-radius:5%;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.5s}.card-front,.card-back{display:flex;justify-content:center;align-items:center}.card-front{background-color:#fff}.card-back{background-color:#ffa000}.card:hover{-webkit-transform:scale(1.06);transform:scale(1.06)}

*{margin:0;padding:0;outline:0;box-sizing:border-box}body{font:400 14px Roboto, sans-serif;background:#f5f5f5;-webkit-font-smoothing:antialised}

